import { Editor } from "../wed";
import { Modal } from "./modal";
import { Scroller } from "./scroller";
import { Direction, SearchReplace } from "./search-replace";

export { Direction };

export class ModalSearchReplace {
  private readonly search: SearchReplace;
  private readonly modal: Modal;

  constructor(editor: Editor,
              scroller: Scroller,
              direction: Direction) {
    this.search = new SearchReplace(editor, scroller, direction);
    this.modal = editor.makeModal();
  }
}
